.nav_left ul{
    display: flex;
    text-decoration: none;
    list-style: none;
    justify-content: start;
    align-items: center;
    padding-left: 1vw;
    

}
.nav_left{
    display: flex;
    justify-content:center;
    align-items: center;
}
.logo{
    font-family: "League Spartan";
}
.nav_left ul li{
    /* font-family: "League Spartan",Helvetica,"Helvetica Neue",Arial,sans-serif; */
    font-size: .8125rem;
    letter-spacing: .05rem;
    line-height: 1rem;
    font-weight: 200;
    margin-right: 2vw ;
}
.nav{
    display: flex;
    justify-content: space-between;
    /* margin-top: 1vh; */
    margin-bottom: 1vh;
    
}

.nav_right{
    display: flex;
    justify-content:center;
    align-items: center;
}
.search{
    display: flex;
    margin-left: 4vw;
    height: 30px;
    background-color: #dfe6e9;
    padding-left: 1vw;
    border-radius: 5px;
}
.search img{
    margin-right: 1vw;
}
.search input{
    outline: none;
    border: none;
    width: 20vw;
    background-color: #dfe6e9;
}
