.slider {
    text-align: center;
}
.slider button{
    background-color: black;
    color: white;
    border: none;
    outline: none;
    height: 7vh;
    width: 20vw;
}
.slider h1{
    font-family: "League Spartan";
}

.main_content{
    display: flex;
    width: 100vw;
    border-top: 1px solid #dfe6e9;
    border-bottom: 1px solid #dfe6e9;
    margin-top: 5vh;
}
.content_left{
    /* flex: 1 100%; */
    width: 50vw;
    /* height: 150vh; */
    border-right: 1px solid #dfe6e9;
}
.content_left img{
    margin: 3vh;
}
.content_right_item{
    display: flex;
    width: 100vh;
    border-bottom: 1px solid #dfe6e9;
    height: 60vh;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: bold;
    flex-direction: column;
    font-family: "League Spartan";
}
.content_right_item_under{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    height: 50vh;
    width: 100vh;

}
.content_right_item_under h2{
    text-align: center;
    font-family: "League Spartan";
}

.content_right_item button{
    margin-top: 3vh;
    height: 13vh;
    background-color: black;
    color: white;
    border: none;
    outline: none;
    width: 15vw;
}
.content_right_item_under img{
    margin: 3vh;
}
.content_left h2{
    text-align: center;
    font-family: "League Spartan";
}
.thumbnail{
    display: flex;
    justify-content: space-around;
    width: 100vw;
    /* margin-top: 10vh; */
}
.thumbnail_1, .thumbnail_2{
    

    display: flex;
    flex-direction: column; 
    align-items: center;
 width: 33.333333vw;
border-right: 1px solid #dfe6e9; 
}
.thumbnail_3{

    display: flex;
    flex-direction: column; 
    align-items: center;
 width: 33.333333vw;
 
}
.thumbnail p{
    margin-top: 5vh;
    font-family: "League Spartan";
}
.thumbnail_1 img, .thumbnail_2 img, .thumbnail_3 img{
    margin-top: 5vh;
}
